const swiper = new Swiper('.swiper-container', {
	// Optional parameters
	loop: false,
	slidesPerView: 4,
	speed: 1000, //    
	effect: 'slide', //   ('slide'  )
	grabCursor: true, //    
	simulateTouch: true, //     
	touchRatio: 1, //    
	touchAngle: 45,
	allowTouchMove: true, //    
	mousewheel: {
		enabled: true, //       
	},
	//     Bootstrap
	breakpoints: {
		320: { //    320px  
			slidesPerView: 1,
			spaceBetween: 0
		},
		576: { //  Bootstrap sm
			slidesPerView: 2,
			spaceBetween: 20
		},
		768: { //  Bootstrap md
			slidesPerView: 2,
			spaceBetween: 40
		},
		992: { //  Bootstrap lg
			slidesPerView: 3,
			spaceBetween: 20
		},
		1200: { //  Bootstrap xl
			slidesPerView: 4,
			spaceBetween: 30
		}
	},
	// If we need pagination
	pagination: {
		el: '.swiper-pagination',
		type: 'fraction',
		renderFraction: function (currentClass, totalClass) {
			return '<span class="' + currentClass + '"></span>' +
				'  ' +
				'<span class="' + totalClass + '"></span>';
		},
	},


	// Navigation arrows
	navigation: {
		nextEl: '.next-class', //   .next-class
		prevEl: '.prev-class'  //   .prev-class
	},


	// And if we need scrollbar
	scrollbar: {
		el: '.swiper-scrollbar',
	},
});
